            <b-form-invalid-feedback :state="isValid(errormsg.username)">{{ errormsg.username }}</b-form-invalid-feedback>
            
          </b-form-group>
          <b-form-group>
            <b-form-input v-model="form.password" type="password" placeholder="" :state="isValid(errormsg.password)"></b-form-input>
            <b-form-invalid-feedback :state="isValid(errormsg.password)">{{ errormsg.password }}</b-form-invalid-feedback>
          </b-form-group>
          <b-form-group>
            <b-form-input v-model="form.email" type="email" placeholder="" :state="isValid(errormsg.email)"></b-form-input>
            <b-form-invalid-feedback :state="isValid(errormsg.email)">{{ errormsg.email }}</b-form-invalid-feedback>
          </b-form-group>
          <b-form-group>
            <b-form-input v-model="form.nickname" placeholder="" :state="isValid(errormsg.nickname)"></b-form-input>
            <b-form-invalid-feedback :state="isValid(errormsg.nickname)">{{ errormsg.nickname }}</b-form-invalid-feedback>
          </b-form-group>
          <b-button type="submit" variant="outline-primary" class="fleft"></b-button>
        </b-form>
        
      </div>
      
    </b-container>
    <br>
    
    <b-container class="mt-3" ><h5 style="text-align: center;">   </h5>
    <div role="tablist">
    <b-card no-body class="mb-1">
      <b-card-header header-tag="header" class="p-1" role="tab">
        <b-button block href="#" v-b-toggle.accordion-1 variant="info"> </b-button>
      </b-card-header>
      <b-collapse id="accordion-1" visible accordion="my-accordion" role="tabpanel">
        <b-card-body>
          
          <b-card-text style="font-size:15px;"><ul><li><>            .</li> 
          <li>        , <>                     .</li>
          <li>, <>          , ,      ,               .</li>
        <li><>   30                       .</li>
</ul></b-card-text>
        </b-card-body>
      </b-collapse>
    </b-card>

    <b-card no-body class="mb-1">
      <b-card-header header-tag="header" class="p-1" role="tab">
        <b-button block href="#" v-b-toggle.accordion-2 variant="info">  </b-button>
      </b-card-header>
      <b-collapse id="accordion-2" accordion="my-accordion" role="tabpanel">
        <b-card-body>
          <b-card-text style="font-size:15px;">
            <ul>
              <li>     .              18         .</li>
              <li> .  </li>
              <li>          </li>
              </ul>
          </b-card-text>
        </b-card-body>
      </b-collapse>
    </b-card>

    <b-card no-body class="mb-1">
      <b-card-header header-tag="header" class="p-1" role="tab">
        <b-button block href="#" v-b-toggle.accordion-3 variant="info"> 3 </b-button>
      </b-card-header>
      <b-collapse id="accordion-3" accordion="my-accordion" role="tabpanel">
        <b-card-body>
          <b-card-text style="font-size:15px;" ><ul>
            <li>
              <>  ,       17  18    3 .
            </li>
            <li>
              <> >       , ,         () , <   >        .
              </li>
            </ul>
          </b-card-text>
        </b-card-body>
      </b-collapse>
      
    </b-card>
  </div><div class="form-check">
    <input type="checkbox">
    <label ></label>
    <input type="checkbox">
    <label > </label>
    </div>
    </b-container>
  </div>
</template>
<script>
import axios from "axios";
import * as session from "../utils/loginService.js";

export default {
  name: "register",
  data() {
    return {
      form: {
        username: "",
        password: "",
        email: "",
        nickname: ""
      },
      errormsg: {
        username: "",
        password: "",
        email: "",
        nickname: ""
      }
    };
  },
  computed: {
    
  },
  components: {},
  methods: {
    onSubmit(evt) {
      evt.preventDefault();
      var payloads = {
        username: this.form.username,
        password: this.form.password,
        email: this.form.email,
        nickname: this.form.nickname
      };

      this.errormsg.username = "";
      this.errormsg.password = "";
      this.errormsg.email = "";
      this.errormsg.nickname = "";

      var nthis = this;

      axios
        .put(session.apiurl + "login", payloads)
        .then(function(response) {
          alert(" .   .");
          nthis.$router.push({ name: "login" });
        })
        .catch(function(error) {
          if (error.response) {
            if (error.response.status == 409 || error.response.status == 400) {
              let errdat = error.response.data;
              if (errdat['username']) {
                nthis.errormsg.username = errdat['username'];
              }
              if (errdat['password']) {
                nthis.errormsg.password = errdat['password'];
              }
              if (errdat['email']) {
                nthis.errormsg.email = errdat['email'];
              }
              if (errdat['nickname']) {
                nthis.errormsg.nickname = errdat['nickname'];
              }

              return;
            }
          }
          alert("   ");
        });
    },

    isValid(errmsg) {
      if (errmsg == "") {
        return null;
      }
      return false;
    }
  }
};
</script>
<style scoped>
.fleft{
  float: right;
}
/* PC */
@media (min-width: 1200px) { 
    .container{
        max-width: 400px;
    }
}

/* PC and Tablet */
@media (min-width: 992px) {
